button{
    cursor: pointer;
}

.googlemap{
    max-width: 100%;
    margin-top: 2vh;
    overflow: hidden;
    -webkit-box-shadow: 5px 5px 15px -1px #000000; 
    box-shadow: 5px 5px 15px -1px #000000;

}

.contactcontainer{
    display: flex;
    justify-content: center;
    width: 100vw;
    padding-top: 2vh;
    font-size: 1.2rem;

}

.onecolumn{
    display: flex;
    flex-direction: column;
    align-items: center;

}

.threecolumns{
    display: flex;

    flex-direction: row;
}

.adresscontainer{
    width: 33%;
    div{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.phoneandemailcontainer{
    width: 33%;
    div{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    :first-child{
        margin-bottom:2vh;
    }
}

.socialmediacontainer{
    width: 33%;
    div{
        display: flex;
        padding-left: 5vw;
    }
    .socialmediatitle{
        margin-bottom:2vh;
        padding-left: 8vw;
    }
    a{
        line-height: 1.2rem;
    }
    
}